@media only screen and (min-width: 820px) {
  .flexbox {
    display: flex;
  }
  .sidebar {
    width: 25vw;
    height: 100vh;
  }
  .sidebar > button {
    width: 100%;
  }
  .content {
    width: 75vw;
    height: 100vh;
    padding-inline: 12.5vw;
  }
}

.sidebar > button {
  width: 100%;
}

* {
  box-sizing: border-box;
}

button, input {
  font-size: inherit;
}
label {
  display: inline-flex;
}
